import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Tag } from "carbon-components-react";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [EventAPI.PILL_CLICKED];

/*
    Example: <srs-pill color="red" content="This is a red pill!"/>
*/
const SrsPill = function (props, ref) {

    const handleClose = (evt) => {
        props.dispatchEvent(EventAPI.PILL_CLICKED, { payload: {id: props.id}});
    }

    return <Tag type={props.color} onClose={handleClose}>{props.content}</Tag>;
}

const styles = `
    
`
const props = [{
    name: "content",
    defaultValue: "Pill content",
    type: "string"
}, {
    name: "color",
    defaultValue: "",
    type: "string",
    metadata: {
        choiceValues: {
            red: 'Red',
            magenta: 'Magenta',
            purple: 'Purple',
            blue: 'Blue',
            cyan: 'Cyan',
            teal: 'Teal',
            green: 'Green',
            gray: 'Gray',
            'cool-gray': 'Cool-Gray',
            'warm-gray': 'Warm-Gray',
            'high-contrast': 'High-Contrast',
            outline: 'Outline'
        }
    }
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsPill), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };